/* eslint-disable react-hooks/exhaustive-deps */
import React,{ useEffect } from "react";
import styled from 'styled-components';
import { useCourse } from "./hooks/useCourse";
const MainContainer = styled.div`
    width:100%;
    position: relative;
    padding-top: 120px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
`
const IntroWrapper = styled.div`
    padding-top:0px;
    text-align: center;
    height: 670px;
    width: 100%;
    position: relative;
    overflow:hidden
`
const BackgroundImg = styled.img`
    height: 670px;
    width: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: -1;
    filter: blur(10px);
    transform: scale(1.2);
`
const MainWrapper = styled.div`
    text-align: center;
    padding-top: 200px;
    padding-left: 30px;
    padding-right:30px;
    margin-left: 100px;
    margin-right: 100px;
`
const BlueAreaWrapper = styled.div`
    width: 100%;
    text-align: center;
    padding-top: 90px;
    padding-bottom: 50px;
    padding-left: 15px;
    padding-right: 15px;
    color: white;
    background-color: #00008B;
`
const WhiteAreaWrapper = styled.div`
    width: 100%;
    text-align: center;
    padding-top: 90px;
    padding-bottom: 50px;
    padding-left: 15px;
    padding-right: 15px;
    color: black;
    background-color: white;
`
const MainPage = () => {
    const { scrollToPosition } = useCourse();
    useEffect(() => {
        scrollToPosition();
    },[]);

    return (
        <MainContainer>
            <IntroWrapper>
                <BackgroundImg src={"https://imgur.com/us2l97Q.jpg"} alt="" />
                <MainWrapper>
                    <h1 style={{color: "white",fontSize: "40px"}}>| ? |</h1>
                    <h3 style={{color: "white",fontSize: "36px"}}>| ? |</h3>
                    <h5 style={{color: "white",fontSize: "32px"}}><strong></strong></h5>
                </MainWrapper>
            </IntroWrapper>
            <BlueAreaWrapper>
                <h1 style={{fontSize: "40px"}}>|  |</h1>
                <h3></h3>
                <h3>,</h3>
            </BlueAreaWrapper>
            <WhiteAreaWrapper>
                <h1 style={{fontSize: "40px"}}>| How to use |</h1>
                <h3></h3>
                <h3>!</h3>
            </WhiteAreaWrapper>
            <BlueAreaWrapper>
                <h1 style={{fontSize: "40px"}}>|  |</h1>
                <h3></h3>
            </BlueAreaWrapper>
            <WhiteAreaWrapper></WhiteAreaWrapper>
        </MainContainer>
    )
}
export default MainPage